import { BrowserRouter, Route, Switch } from 'react-router-dom';
import "./App.css";
import Login from './Authentication/form-login';
import Register from './Authentication/form-register';
import NotFound from './NotFound/not-found';

function App() {
  return (
    <BrowserRouter>
        <div className="App">
            <Switch>
                <Route path="/nguyenvanlinh-cv/login" component={Login}></Route>
                <Route path="/nguyenvanlinh-cv/register" component={Register}></Route>
                <Route path="/*" component={NotFound}></Route>
            </Switch>
        </div>
    </BrowserRouter>
  );
}

export default App;
